import React from "react";
import PropTypes from "prop-types";
import styled from "styled-components";
import { connect } from "react-redux";

import AddRowInTable from "./addRowInTable/AddRowInTable";
import TableRow from "./tableRow/TableRows";

import { deleteRow, changeFieldRow, addRow } from "../../../ac/index";

const Table = ({ table, deleteRow, changeFieldRow, addRow }) => {
  return (
    <Wrap>
      <AddRowInTable addRow={addRow} />
      <Container>
        {table.map(el => {
          const { id, name, surname } = el;
          return (
            <TableRow
              key={id}
              id={id}
              name={name}
              surname={surname}
              deleteRow={deleteRow}
              changeFieldRow={changeFieldRow}
            />
          );
        })}
      </Container>
    </Wrap>
  );
};

export default connect(
  state => ({
    table: state.table
  }),
  { deleteRow, changeFieldRow, addRow }
)(Table);

Table.propTypes = {
  table: PropTypes.array.isRequired,
  addRow: PropTypes.func.isRequired,
  deleteRow: PropTypes.func.isRequired,
  changeFieldRow: PropTypes.func.isRequired
};

const Wrap = styled.div``;

const Container = styled.div`
  margin: 3rem 0 0 0;
  border: 1px solid rgba(212, 212, 212, 0.5);

  > div:nth-child(even) {
    background: #d4d4d480;
  }
`;



