import { injectGlobal } from "styled-components";

injectGlobal`
	:root {
		--primary: rgb(133, 232, 255);
		--secondory: rgb(178, 34, 34);
	}

	html {
		font-size: 10px;
	}

	body {
		margin: 0;
	}

	menu,
	ul,
	li,
	h1,
	h2,
	h3,
	h4,
	h5,
	h6,
	p,
	a {
		list-style: none;
		padding: 0;
		margin: 0;
		text-decoration: none;
		color: inherit;
	}


	p, a, span, button, input, label {
		font-size: 2rem;
	}
`;



