import React from "react";
import styled from "styled-components";

const Readme = () => (
  <Wrap>
    <H3>Задачи:</H3>
    <Ul>
      <Li>1. Добавление строк в таблицу.</Li>
      <Li>2. Удаление строк из таблици.</Li>
      <Li>3. Редактирование ячеек, кроме поля ID.</Li>
      <Li>Обязтельное условие: испольование redux, хранение таблици в нём.</Li>
    </Ul>
    <br />
    <H3>Возможные варианты решение:</H3>
    <Ul>
      <Li>
        1. Использование одного connect к redux на главном копоненте, и
        прокидывать вниз props.
      </Li>
      <Li>
        2. Отрисовывая таблицу в каждую строку прокидывать только ID и отдельно
        подключать к redux.
      </Li>
    </Ul>
    <br />
    <H3>Использованное решение:</H3>
    <P>Выл выбран первый вариант, по причинам:</P>
    <Ul>
      <Li>1. Меньшего количества подключение к redux</Li>
      <Li>
        2. При подключении каждой строки к redux, у нас так же будет происходит
        оповещение главного компонента т.к. он должен быть подключен к redux для
        отрисовки таблици
      </Li>
    </Ul>
    <br />
    <H3>Улучшение проекта:</H3>
    <Ul>
      <Li>
        1. При большом объеме строк, использовать библиотку
        <A
          href="https://github.com/react-tools/react-table"
          target="_blank"
          rel="noopener noreferrer"
        >
          react-table
        </A>
        либо другую со схожим функционалом.
      </Li>
      <Li>
        2. Реализовать второй вариант для сравнение производительности.
        Использовать PureCopmonent для оптимизации главного компонента, либо
        другие средства(методы жизненного цикл, и т.д.)
      </Li>
    </Ul>
  </Wrap>
);

export default Readme;

const Wrap = styled.div``;

const H3 = styled.h3`
  font-size: 4.6rem;
`;

const Ul = styled.ul``;

const Li = styled.li`
  font-size: 2.2rem;
`;

const P = styled.p`
  font-size: 2rem;
`;

const A = styled.a`
  font-size: 2.2rem;
  font-weight: 700;
  margin: 0 10px;
  text-decoration: underline;
`;



