import { ADD_ROW, DELETE_ROW, CHANGE_FIELD } from "../constants";

export const addRow = ({ id, name, surname }) => ({
  type: ADD_ROW,
  payload: {
    name,
    surname
  }
});

export const deleteRow = id => ({
  type: DELETE_ROW,
  payload: id
});

export const changeFieldRow = ({ id, value, typeField }) => ({
  type: CHANGE_FIELD,
  payload: {
    id,
    value,
    typeField
  }
});



