import { ADD_ROW, DELETE_ROW, CHANGE_FIELD } from "../constants";

const initialState = [
  {
    id: 1,
    name: "Name1",
    surname: "Surname1"
  },
  {
    id: 2,
    name: "Name2",
    surname: "Surname2"
  }
];

export default (state = initialState, { type, payload }) => {
  switch (type) {
    case ADD_ROW:
      return [
        ...state,
        {
          id: state.reduce((maxId, row) => Math.max(row.id, maxId), -1) + 1,
          name: payload.name,
          surname: payload.surname
        }
      ];
    case DELETE_ROW:
      return state.filter(row => row.id !== payload);
    case CHANGE_FIELD:
      return state.map(row => {
        const { id, typeField, value } = payload;
        return row.id === id ? { ...row, [typeField]: value } : row;
      });
    default:
      return state;
  }
};



