import React, { Component } from "react";
import PropTypes from "prop-types";
import styled from "styled-components";

import RowInput from "../../../components/input/RowInput";
import DeleteButton from "../../../components/button/DeleteButton";

class TableRows extends Component {
  static propTypes = {
    id: PropTypes.number.isRequired,
    name: PropTypes.string,
    surname: PropTypes.string,
    deleteRow: PropTypes.func.isRequired,
    changeFieldRow: PropTypes.func.isRequired
  };

  handleOnChange = (e, typeField) => {
    const { id } = this.props;
    this.props.changeFieldRow({ value: e.target.value, id, typeField });
  };

  render() {
    const { id, name, surname } = this.props;
    return (
      <Wrap id={id}>
        <Id>{id}</Id>
        <RowInput value={name} onChange={e => this.handleOnChange(e, "name")} />
        <RowInput
          value={surname}
          onChange={e => this.handleOnChange(e, "surname")}
        />
        <DeleteButton onClick={() => this.props.deleteRow(id)}>
          Удалить строку
        </DeleteButton>
      </Wrap>
    );
  }
}

export default TableRows;

const Wrap = styled.div`
  display: grid;
  grid-template-columns: 5rem 1fr 1fr min-content;
  align-items: baseline;
  padding: 0.5rem 0;
`;

const Id = styled.p`
  text-align: center;
`;



